import React from "react";
import {createRoot} from "react-dom/client";
import "@mailtracker/pages/popup/index.css";
import Popup from "@mailtracker/pages/popup/Popup";
// @ts-ignore
import refreshOnUpdate from "virtual:reload-on-update-in-view";
import {MailTrackerStyle} from "@front-component";
import {App} from "antd";

refreshOnUpdate("pages/popup");

function init() {
  const appContainer = document.querySelector("#app-container");
  if (!appContainer) {
    throw new Error("Can not find #app-container");
  }
  const root = createRoot(appContainer);
  root.render(<MailTrackerStyle><App><MailTrackerStyle><Popup /></MailTrackerStyle></App></MailTrackerStyle>);
}

init();
